@media all and (min-width:0px) and (max-width: 768px) {
 
  .logo {
    height: 220px !important;
    width: auto !important;
    margin: 0;
  }

  .link {
    margin-bottom: 40px;
  }

}


.lineup {
  text-align: center;
}

body{
  overflow: hidden;
}

.time-container {
  background-color: red;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 30px;
}

.App {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin: 0;
  
}


.content-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.logo{
  height: 60px;
  margin: 50px;
  fill: black;
  
}

@media only screen and (max-width: 37.5em) {
  .logo{
    height: 40px;
    scale: .5;
    margin: 10px;
    fill: black;
  }
}

.App-logo {
  margin-top: 300px;
  margin-bottom: 0;
  scale: .3;
}

.error-text {
  color: red;
}

.link {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  margin-top: 40px;
  color: white;
  background-color: black;
  width: 150px;
  height: 40px;
  border-radius: 5px;
}




